/*Header layout*/
.cafe-absolute-header {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    width: 100%;
}

.is-sticky {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 11;
}

.cafe-row-sticky {
    width: 100%;
    &.cafe-scroll-up-sticky {
        &.is-sticky {
            transition: all 350ms ease !important;
        }
        &.is-sticky:not(.active-sticky) {
            top: -100%;
        }
    }
}

@media(min-width: 600px) and (max-width: 782px) {
    .admin-bar {
        .cafe-row-sticky {
            &.is-sticky {
                margin-top: 46px
            }
        }
    }
}

@media(min-width: 783px) {
    .admin-bar {
        .cafe-row-sticky {
            &.is-sticky {
                margin-top: 32px
            }
        }
    }
}

/*End Header layout*/
/*Site Logo*/
.elementor-widget-clever-site-logo {
    font-size: 0;
}

.cafe-wrap-site-logo {
    display: block;
    width: 100%;
    position: relative;
    img {
        max-width: 100%;
        max-height: 100%;
        transition: opacity 300ms;
    }
}

.cafe-wrap-sticky-logo {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
    width: 100%;
    text-align: inherit;
    display: block;
    opacity: 0;
    visibility: hidden;
    transition: all 300ms;
}

.cafe-site-logo {
    transition: all 300ms;
    display: inline-block;
    img {
        transition: all 300ms;
    }
}

.cafe-inner-sticky-logo {
    display: inline-block;
}

.cafe-row-sticky.is-sticky {
    .has-sticky-logo {
        .cafe-site-logo {
            opacity: 0;
            visibility: hidden;
        }
    }
    .cafe-wrap-sticky-logo {
        opacity: 1;
        visibility: visible;
    }
}

.cafe-scroll-up-sticky {
    .cafe-site-logo {
        transition: none;
        img {
            transition: none;
        }
    }
    .cafe-wrap-sticky-logo {
        transition: none;
    }
}

/*End Site Logo*/
/*Site Navigation Menu*/
.cafe-site-menu li>.cmm4e-sub-panel.pos-left{
    left: auto !important;
    right: calc(100% + 20px) !important;
}
.cafe-site-menu {
    --menu-color: #000;
    --menu-hover-color: #212121;
    --menu-bg: transparent;
    --menu-hover-bg: #2980b9;
    --menu-icon-color: #000;
    --menu-mask-bg: rgba(0, 0, 0, 0.6);
    font-size: 0;
    .cafe-menu {
        font-size: 0;
    }
    .cafe-menu, .menu-item > ul {
        margin: 0
    }
    .menu-item {
        font-size: 1rem;
        padding: 0;
        list-style: none;
        position: relative;
        width: 100%;
        & > a {
            padding: 10px 25px 10px 15px;
            display: block;
            position: relative;
            line-height: 1.3;
            overflow: hidden;
            z-index: 1;
            color: var(--menu-color);
            background: var(--menu-bg);
        }
        &.current-menu-item,
        &.current-menu-parent,
        &:hover {
            & > a {
                color: var(--menu-hover-color);
                background: var(--menu-hover-bg);
            }
        }
    }
    /*Menu arrow*/
    .cafe-menu-arrow {
        line-height: 1;
        color:inherit;
        &:before {
            font-family: cleversoft;
            font-size: 65%;
        }
    }
    .menu-item .menu-item .cafe-menu-arrow {
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }
    .cafe-menu-arrow {
        &:before {
            content: '\c059';
        }
        &.active:before {
            content: '\c009';
        }
    }
    &.icon-submenu-carret {
        .cafe-menu-arrow {
            &:before {
                content: '\c264';
            }
            &.active:before {
                content: '\c261';
            }
        }
    }
    &.icon-submenu-plus {
        .cafe-menu-arrow {
            &:before {
                content: '\c025';
            }
            &.active:before {
                content: '\c035';
            }
        }
    }
    //horizontal layout
    &.horizontal {
        &.icon-submenu-arrow {
            .menu-item .menu-item .cafe-menu-arrow {
                &:before {
                    content: '\c034';
                }
            }
        }
        .menu-item {
            & > ul {
                position: absolute;
                background: $white_text;
                box-shadow: -2px 2px 81px -17px rgba(0, 0, 0, 0.29);
                width: 280px;
                left: 100%;
                top: 30px;
                z-index: 3;
                opacity: 0;
                visibility: hidden;
                transition: all 300ms;
                &.pos-left {
                    right: 100%;
                    left: auto;
                }
            }
            &:hover > ul {
                top: 0;
                opacity: 1;
                visibility: visible;
            }
        }
        .cafe-menu > .menu-item {
            display: inline-block;
            width: auto;
            &:first-child {
                margin-left: 0 !important;
            }
            &:last-child {
                margin-right: 0 !important;
            }
            a {
                padding: 10px 15px;
                & > .cafe-menu-arrow {
                    margin-left: 3px;
                }
            }
            & > ul {
                left: 0;
                top: calc(100% + 30px);
                &.pos-left {
                    right: 0;
                    left: auto;
                }
            }
            &:hover > ul {
                top: 100%;
            }
            .menu-item > a {
                padding: 8px 25px 8px 15px;
            }
        }
    }
    //Vertical layout
    &:not(.horizontal) {
        &:not(.icon-submenu-none) {
            .menu-item > ul {
                display: none;
            }
        }
        .cafe-menu > .menu-item {
            width: 100%;
            display: block;
            .cafe-menu-arrow {
                position: absolute;
                width: 30px;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }
            & > ul {
                padding-left: 10px;
                width: 100%;
            }
        }
    }
    /*Hover Style*/
    &.hover-style-normal {
        --menu-hover-bg: transparent;
        .cafe-menu > .menu-item {
            & > a:before {
                height: 100%;
                right: 0;
                bottom: 0;
                visibility: visible;
                opacity: 0;
            }
            &.current-menu-item,
            &.current-menu-parent,
            &:hover {
                & > a:before {
                    opacity: 1;
                }
            }
        }
    }
    &.hover-style-slide {
        --menu-hover-color: #fff;
    }
    &:not(.hover-style-slide) {
        .cafe-menu > .menu-item {
            & > a {
                &:before {
                    content: '';
                    display: block;
                    transition: inherit;
                    visibility: hidden;
                    position: absolute;
                    background: var(--menu-hover-bg);
                    width: 100%;
                    z-index: -1;
                }
            }
        }
    }
    &.hover-style-underline {
        .cafe-menu > .menu-item {
            & > a:before {
                height: 3px;
                left: 0;
                bottom: 50%;
                visibility: visible;
                opacity: 0;
            }
            &.current-menu-item,
            &.current-menu-parent,
            &:hover {
                & > a:before {
                    bottom: 0;
                    opacity: 1;
                }
            }
        }
    }
    &.hover-style-underline-2 {
        .cafe-menu > .menu-item {
            & > a {
                background: transparent;
            }
            & > a:before {
                height: 1em;
                left: 0;
                top: 50%;
                width: 0;
                visibility: visible;
                transition: 300ms ease-in-out;
            }
            &.current-menu-item,
            &.current-menu-parent,
            &:hover {
                & > a:before {
                    width: 100%;
                }
            }
        }
    }&.hover-style-underline-3 {
        .cafe-menu > .menu-item {
            & > a {
                background: transparent;
            }
            & > a:before {
                height: 1px;
                left: 0;
                bottom: 0;
                width: 0;
                visibility: visible;
                transition: 300ms ease-in-out;
            }
            &.current-menu-item,
            &.current-menu-parent,
            &:hover {
                & > a:before {
                    width: 100%;
                }
            }
        }
    }
    &.hover-style-line-through {
        .cafe-menu > .menu-item {
            & > a:before {
                height: 2px;
                left: 50%;
                width: 0;
                bottom: calc(50% - 1px);
                visibility: visible;
                opacity: 1;
            }
            &.current-menu-item,
            &.current-menu-parent,
            &:hover {
                & > a:before {
                    width: 100%;
                    left: 0;
                }
            }
        }
    }
    &.hover-style-line-through-2 {
        .cafe-menu > .menu-item {
            & > a:before {
                height: 2px;
                left: 0;
                width: 0;
                bottom: calc(50% - 1px);
                visibility: visible;
                opacity: 1;
            }
            &.current-menu-item,
            &.current-menu-parent,
            &:hover {
                & > a:before {
                    width: 35%;
                }
            }
        }
    }
    &.hover-style-double-lines {
        .cafe-menu > .menu-item {
            & > a:after {
                content: '';
                display: block;
                position: absolute;
                height: 2px;
                width: 0;
                right: 0;
                top: 0;
                background: var(--menu-hover-bg);
                z-index: -1;
                transition: inherit;
            }
            & > a:before {
                height: 2px;
                left: 0;
                bottom: 0;
                visibility: visible;
                width: 0;
                opacity: 1;
            }
            &.current-menu-item,
            &.current-menu-parent,
            &:hover {
                & > a:before, & > a:after {
                    width: 100%;
                }
            }
        }
    }

    .cafe-hamburger-input-control, .cafe-hamburger-button {
        display: none;
    }
    .cafe-hamburger-mask {
        width: 100vw;
        height: 100vh;
        background: var(--menu-mask-bg);
        opacity: 0;
        visibility: hidden;
        transition: all 500ms;
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        cursor: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='32' height='32' viewBox='0 0 18 18' fill='%23fff'%3e%3cpath d='M14.53 4.53l-1.06-1.06L9 7.94 4.53 3.47 3.47 4.53 7.94 9l-4.47 4.47 1.06 1.06L9 10.06l4.47 4.47 1.06-1.06L10.06 9z'/%3e%3c/svg%3e"), pointer;
    }
    .cafe-hamburger-button {
        position: relative;
        transition: .5s ease-in-out;
        cursor: pointer;
        color: var(--menu-icon-color);
        align-items: center;
        font-size: 1rem;
        .cafe-wrap-hamburger-icon{
            position: relative;
            width: 24px;
            height: 24px;
            display: inline-block;
        }
        .cafe-hamburger-icon {
            width: 100%;
            top: calc(50% - 1px);
            left: 50%;
            height: 2px;
            background: var(--menu-icon-color);
            transform-origin: center;
            transition: .5s ease-in-out;
            display: block;
            position: absolute;
            transform: translateX(-50%);
            &:after,
            &:before {
                transition: .5s ease-in-out;
                content: "";
                position: absolute;
                display: block;
                width: 100%;
                height: 100%;
                background: var(--menu-icon-color);
            }
            &:before {
                top: -8px
            }
            &:after {
                bottom: -8px
            }
        }
    }
    .cafe-hamburger-close-button {
        display: none;
        cursor: pointer;
        color: var(--menu-color);
    }
    .cafe-wrap-menu {
        .toggle {
            position: relative;
            .arrow {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                z-index: 1;
                font-size: 12px;
                cursor: pointer;
                &:before{
                    position: absolute;
                    top: 50%;
                    right: 15px;
                    z-index: 2;
                    transform: translateY(-50%);
                    -webkit-transform: translateY(-50%);
                    -moz-transform: translateY(-50%);
                    transition: all 350ms;
                }
            }
        }
        .wrap-menu-inner {
            position: absolute;
            top: 100%;
            left: 0;
            right: 0;
            width: 100%;
            z-index: 1;
        }
        &.toggle-active {
            .toggle{
                .arrow {
                    &:before{
                        transform: translateY(-50%) rotate(180deg);
                        -moz-transform: translateY(-50%) rotate(180deg);
                        -webkit-transform: translateY(-50%) rotate(180deg);
                    }
                }
            }
        }
    }
    input.cafe-hamburger-input-control:checked ~ .cafe-hamburger-button {
        .cafe-hamburger-icon {
            transform: translateX(-50%) rotate(45deg);
            &:after {
                transform: rotate(90deg);
                bottom: 0;
            }
            &:before {
                transform: rotate(90deg);
                top: 0;
            }
        }
    }
    &.hamburger {
        --menu-bg: #fff;
        --menu-hover-bg: transparent;
        .menu-item > ul {
            display: none;
        }
        .cafe-hamburger-button{
            display: inline-flex;
        }
        &.cafe-hamburger-fixed-tr-fs-effect {
            .cafe-wrap-menu {
                position: fixed;
                left: 50vw;
                top: 50vh;
                transform: translate(-50%, -50%);
                width: 100%;
                max-width: 360px;
                opacity: 0;
                visibility: hidden;
                transition: .5s ease-in-out;
                overflow: hidden;
                overflow-y: auto;
                max-height: calc(100vh - 60px);
                margin-top: 30px;
                z-index: 999;
            }
            .menu-item > a {
                background: transparent;
            }
            .cafe-hamburger-button {
                display: block;
                position: fixed;
                right: 50px;
                top: 50px;
                z-index: 100;
                width: 40px;
                height: 40px;
                padding: 8px;
                border-radius: 50%;
                background: var(--menu-bg);
                box-shadow: 0 0 0 0 var(--menu-bg), 0 0 0 0 var(--menu-bg);
            }
            input.cafe-hamburger-input-control:checked ~ .cafe-hamburger-button {
                box-shadow: 0 0 0 100vw var(--menu-bg), 0 0 0 100vh var(--menu-bg);
            }
            input.cafe-hamburger-input-control:checked ~ .cafe-wrap-menu {
                opacity: 1;
                visibility: visible;
            }
        }
        &.cafe-hamburger-off-canvas-effect {
            .cafe-wrap-menu {
                position: fixed;
                top: 0;
                height: 100vh;
                background: var(--menu-bg);
                width: 360px;
                max-width: calc(100% - 30px);
                padding: 30px 0;
                transition: all 300ms;
                z-index: 99;
            }
            &.left {
                .cafe-wrap-menu {
                    left: 0;
                    transform: translateX(-360px);
                }
            }
            &.right {
                .cafe-wrap-menu {
                    right: 0;
                    transform: translateX(360px);
                }
            }
            .cafe-menu {
                padding: 0 15px;
            }
            .cafe-hamburger-mask {
                display: block;
                z-index: 98;
            }
            input.cafe-hamburger-input-control:checked ~ {
                & .cafe-wrap-menu {
                    transform: translateX(0);
                }
                & .cafe-hamburger-mask {
                    visibility: visible;
                    opacity: 1;
                }
            }
        }
        &.cafe-hamburger-off-canvas-2-effect {
            .cafe-wrap-menu {
                position: fixed;
                top: 0;
                height: 100vh;
                background: var(--menu-bg);
                width: 70vw;
                overflow: hidden;
                overflow-y: auto;
                padding: 30px 5%;
                transition: all 350ms;
                z-index: 99;
                display: flex;
                align-items: center;
            }
            .cafe-hamburger-close-button {
                position: absolute;
                top: 20px;
                left: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 2rem;
                z-index: 9;
                width: 1em;
                height: 1em;
            }
            &.left {
                .cafe-hamburger-close-button {
                    right: 20px;
                    left: auto;
                }
                .cafe-wrap-menu {
                    left: 0;
                    transform: translateX(-70vw);
                }
            }
            &.right {
                .cafe-wrap-menu {
                    right: 0;
                    transform: translateX(70vw);
                }
            }
            .cafe-menu {
                padding: 15px 0;
                width: 100%;
                & > .menu-item {
                    transform: translateY(10px);
                    opacity: 0;
                    transition: 350ms ease-out;
                    transition-delay: 300ms;
                }
                .menu-item > a {
                    font-size: 2.2rem;
                    text-transform: uppercase;
                    font-weight: lighter;
                    .cafe-menu-arrow {
                        display: inline;
                        margin: 0 5px;
                        position: static;
                    }
                }
            }
            .cafe-hamburger-mask {
                display: block;
                z-index: 98;
            }
            input.cafe-hamburger-input-control:checked ~ {
                & .cafe-wrap-menu {
                    transform: translateX(0);
                    transition: all 750ms cubic-bezier(.3, .88, .37, .99);
                    transition-delay: 0ms;
                    .cafe-menu > .menu-item {
                        opacity: 1;
                        transform: translateY(0);
                    }
                }
                & .cafe-hamburger-mask {
                    visibility: visible;
                    opacity: 1;
                }
            }
            @for $i from 1 through 12 {
                input.cafe-hamburger-input-control:checked ~ {
                    & .cafe-wrap-menu {
                        .cafe-menu > .menu-item:nth-child( #{$i}) {
                            transition-delay: calc(#{$i} * 100ms + 450ms);
                        }
                    }
                }
            }
        }
        &.cafe-hamburger-full-screen-effect {
            .cafe-hamburger-close-button {
                top: 20px;
                right: 20px;
                width: 40px;
                height: 40px;
                display: flex;
                justify-content: center;
                align-items: center;
                position: fixed;
                cursor: pointer;
                font-size: 2rem;
            }
            .cafe-wrap-menu {
                width: 100vw;
                height: 100vh;
                position: fixed;
                top: -20px;
                left: 0;
                opacity: 0;
                visibility: hidden;
                z-index: 111;
                display: flex;
                justify-content: center;
                align-items: center;
                overflow: hidden;
                overflow-y: auto;
                padding: 30px 0;
                transition: all 100ms ease-out;
            }
            .cafe-hamburger-mask {
                width: 100vw;
                height: 100vh;
                position: fixed;
                top: -10vh;
                left: 0;
                background: var(--menu-bg);
                opacity: 0;
                visibility: hidden;
                z-index: 110;
                transition: all 200ms;
                display: block;
            }
            .cafe-menu {
                max-width: 100%;
                width: 460px;
                & > li.menu-item {
                    & > a {
                        background: transparent;
                    }
                }
                .menu-item > a {
                    font-weight: normal;
                    font-size: 2rem;
                }
            }

            input.cafe-hamburger-input-control:checked ~ {
                .cafe-hamburger-mask {
                    visibility: visible;
                    opacity: 1;
                    top: 0;
                }
                .cafe-wrap-menu {
                    visibility: visible;
                    top: 0;
                    opacity: 1;
                    transition: opacity 450ms, top 250ms ease;
                }

            }
        }
        &.cafe-hamburger-slide-down-effect {
            .cafe-wrap-menu {
                height: 0;
                overflow: hidden;
                transition: all 450ms ease-out;
            }
            input.cafe-hamburger-input-control:checked ~ {
                .cafe-wrap-menu {
                    padding-top: 15px;
                    padding-bottom: 15px;
                    height: 50vh;
                    overflow-y: auto;
                }
            }
        }
        &.cafe-hamburger-slide-left-effect {
            --menu-bg:transparent;
            --menu-color:#fff;
            .menu-item{
                font-size: 2rem;
            }
            .cafe-wrap-menu {
                position: fixed;
                top: 50%;
                max-height: 100vh;
                overflow: hidden;
                background: var(--menu-bg);
                width: 360px;
                max-width: calc(100% - 30px);
                padding: 30px 0;
                transition: ease-in-out 300ms;
                z-index: 2;
                right: 0;
                transform: translate(360px, -50%);
            }
            .mega-menu-content{
                position: static;
                opacity: 1;
                visibility: visible;
                display: none;
                box-shadow: none;
                background: inherit;
            }
            .cafe-menu {
                padding: 0 15px;
                overflow: hidden;
                overflow-y: auto;
            }
            .cafe-hamburger-mask {
                display: block;
                z-index: 1;
                &:before{
                    content: "\c067";
                    font-family: cleversoft;
                    color: var(--menu-color);
                    position: fixed;
                    top: 15px;
                    right: 15px;
                    font-size: 1.5rem;
                }
            }
        }

    }
    .cafe-wrap-menu-slide-left{
        position: absolute;
    }
}
.hamburger-active{
    .cafe-site-menu.hamburger.cafe-hamburger-slide-left-effect{
        .cafe-wrap-menu{
            transform: translate(0, -50%);
        }
        .cafe-hamburger-mask {
            visibility: visible;
            opacity: 1;
        }
    }
}
.cafe-wrap-body-content{
    transition: ease-in-out 500ms;
    background: #fff;
    z-index: 3;
}
.cafe-hamburger-slide-left-active{
    overflow: hidden;
    .cafe-wrap-body-content{
        width: 100vw;
        height: 100vh;
        transform: scale(0.8) translateX(-380px);
    }
    .cafe-wrap-body-content-inner{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
}
/*Mobile style*/
@media(min-width: 1024.98px) {
    .cafe-site-menu.hamburger.cafe-hamburger-slide-left-effect .cafe-wrap-menu{
        width: 33vw;
        transform: translate(33vw, -50%);
        padding:5vh 5%;
    }
    .cafe-wrap-body-content::after{
        content: "\c067";
        font-family: cleversoft;
        color: #fff;
        display: flex;
        width: 60px;
        height: 60px;
        font-size: 1.3rem;
        background: red;
        border-radius: 50%;
        position: fixed;
        z-index: 1;
        right: 0;
        opacity: 0;
        top: 50%;
        transform: translate(50%, -50%);
        cursor: pointer;
        justify-content: center;
        align-items: center;
        &:hover{
            box-shadow: 0 0 8px rgba(0,0,0,0.1);
        }
    }
