/*Search Form*/
.cafe-wrap-search {
    --border-color: #ebebeb;
    --bg-color: #fff;
    --form-height: 40px;
    --result-color: #000;
}

.cafe-mask-close {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.6);
    transition: 300ms ease-out;
    visibility: hidden;
    opacity: 0;
    z-index: 88;
    cursor: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='32' height='32' viewBox='0 0 18 18' fill='%23fff'%3e%3cpath d='M14.53 4.53l-1.06-1.06L9 7.94 4.53 3.47 3.47 4.53 7.94 9l-4.47 4.47 1.06 1.06L9 10.06l4.47 4.47 1.06-1.06L10.06 9z'/%3e%3c/svg%3e"), pointer;
}

.cafe-toggle-input:checked ~ {
    .cafe-mask-close {
        opacity: 1;
        visibility: visible;
    }
}

.cafe-search-form {
    padding: 0;
    margin: 0;
    input.cafe-search-field {
        margin: 0;
        border: none;
        flex-grow: 1;
        width: auto;
        height: auto;
        min-width: 0;
    }
    .cafe-wrap-product-cat {
        position: relative;
        color: var(--form-color);
        &:after {
            content: '\c059';
            font-family: cleversoft;
            position: absolute;
            right: 5px;
            top: 50%;
            transform: translateY(-50%);
            z-index: 1;
            font-size: 75%;
            color: inherit;
        }
    }
    .cafe-product-cat {
        width: 100%;
        height: 40px;
        padding: 0 20px 0 10px;
        border: none;
        border-left: 1px solid var(--border-color);
        background: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        -webkit-border-radius: 0;
        color: inherit;
        position: relative;
        z-index: 2;
    }
}

.cafe-wrap-search-fields {
    display: flex;
    justify-content: center;
    align-items: stretch;
    border: 1px solid;
    background: var(--bg-color);
    height: var(--form-height);
}

.cafe-search-submit {
    padding: 0 8px;
    white-space: nowrap;
}

.cafe-toggle-input {
    display: none;
}

.cafe-search-toggle-button {
    width: 40px;
    height: 40px;
    display: inline-block;
    color: #000;
    cursor: pointer;
    transition: all 300ms;
    i {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
    }
}

.cafe-wrap-search-result {
    margin: 0;
}

.cafe-search-form {
    .cafe-row {
        margin-left: -10px;
        margin-right: -10px;
        width: calc(100% + 20px);
    }
    .search-result-item {
        margin-bottom: 20px;
        padding-left: 10px;
        padding-right: 10px;
        & > a {
            color: var(--result-color);
        }
        .title-result {
            color: var(--result-color);
            margin-bottom: 5px;
            font-size: 1rem;
        }
        p {
            margin-bottom: 0;
            color: inherit;
        }
    }
    &.result-list-layout {
        .cafe-wrap-search-result {
            padding: 20px 0;
        }
        .search-result-item {
            & > a {
                display: flex;
                align-items: center;
            }
            .wrap-img-result {
                max-width: 80px;
            }
            .wrap-result-content {
                padding: 0 15px;
            }
        }
    }
    &.result-grid-layout {
        .wrap-img-result {
            width: 100%;
            margin-bottom: 10px;
        }
        .wrap-result-content {
            width: 100%;
            text-align: center;
        }
    }
    &.searching, &.searched {
        .cafe-wrap-search-form-content {
            margin-bottom: 0;
        }
    }
    .cafe-wrap-search-result {
        &:before, &:after {
            -webkit-animation: bounce 2.0s infinite ease-in-out;
            animation: bounce 2.0s infinite ease-in-out;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: #3EA4F8;
            position: absolute;
            top: calc(50% - 20px);
            left: calc(50% - 20px);
            content: '';
            visibility: hidden;
            transition: all 300ms;
            opacity: 0;
        }
        &:after {
            -webkit-animation-delay: -1.0s;
            animation-delay: -1.0s;
        }
        & > * {
            transition: all 300ms
        }
    }
    &.searching .cafe-wrap-search-result {
        &:after, &:before {
            opacity: 0.6;
            visibility: visible;
            z-index: 11;
        }
        & > * {
            opacity: 0;
            visibility: hidden;
        }
    }
}

.cafe-wrap-search {
    &:not(.normal) {
        --form-height: auto;
        .cafe-search-form {
            display: flex;
            width: 100vw;
            justify-content: center;
            align-items: center;
            background: var(--bg-color);
            top: 0;
            left: 0;
            position: fixed;
            transition: 350ms ease-out;
            z-index: 105;
            input, button {
                font-size: 2rem;
                padding: 0;
            }
            .cafe-wrap-search-result {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                overflow: hidden;
                overflow-y: auto;
                max-height: 50vh;
                align-content: flex-start;
            }
        }
