            width: 100%;
            flex-direction: column;
            border: none;
            border-bottom: 2px solid var(--border-color);
            background: none;
        }
    }
    &.normal {
        .cafe-wrap-search-result {
            position: absolute;
            left: 0;
            top: 100%;
            opacity: 0;
            visibility: hidden;
            z-index: 22;
            max-height: 60px;
            transition: all 300ms;
            background: var(--bg-color);
