            }
            &.searched {
                .cafe-wrap-search-result {
                    max-height: 50vh;
                }
            }
        }
        .no-result {
            margin-bottom: 20px;
        }
    }
    &.slide-down {
        --border-color: #000;
        --form-color: #000;
        .cafe-search-form {
            height: 100vh;
            transform: translateY(-100vh);
            .cafe-wrap-search-result {
                background: var(--bg-color);
                transition: all 300ms;
                max-height: 20vh;
                position: absolute;
                top: 100%;
                left: 0;
                padding: 20px 20px 0;
                width: 100%;
                opacity: 0;
                visibility: hidden;
            }
            &.searching {
                .cafe-wrap-search-result {
                    height: 60px;
                    overflow-y: hidden;
                }
            }
            &.searched {
                .cafe-wrap-search-result {
                    max-height: 50vh;
                }
            }
        }
        .cafe-toggle-input:checked ~ {
            .cafe-search-form {
                transform: translateY(0);
                &.searching, &.searched {
                    .cafe-wrap-search-result {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
            .cafe-search-close,
            .cafe-search-mask {
                opacity: 1;
                visibility: visible;
                transform: scale(1);
            }
        }
    }
    &.modal {
        --form-color: #000;
        --border-color: #000;
        --bg-color: #fff;
        --result-color: #000;
        .cafe-search-form {
            height: 100vh;
            opacity: 0;
            visibility: hidden;
            .cafe-wrap-search-result {
                height: 50vh;
            }
            .cafe-wrap-search-form-content {
                transition: all 350ms;
                margin-bottom: -50vh;
            }
        }
        .cafe-toggle-input:checked ~ {
            .cafe-search-form {
                opacity: 1;
                visibility: visible;
            }
            .cafe-search-close {
                opacity: 1;
                visibility: visible;
                transform: scale(1);
            }
        }
    }
    .cafe-search-close {
        width: 45px;
        height: 45px;
        font-size: 2rem;
        position: fixed;
        right: 30px;
        top: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        transform: scale(0.5);
        visibility: hidden;
        transition: 450ms ease;
        z-index: 111;
        cursor: pointer;
        color: var(--form-color);
    }
}

@-webkit-keyframes bounce {
    0%, 100% {
        -webkit-transform: scale(0.0)
    }
    50% {
        -webkit-transform: scale(1.0)
    }
}

@keyframes bounce {
    0%, 100% {
        transform: scale(0.0);
        -webkit-transform: scale(0.0);
    }
    50% {
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
    }
}

@media(min-width: 556px) {
    .cafe-search-form .cafe-product-cat {
        height: 100%;
    }
    .cafe-wrap-search.slide-down {
        .cafe-search-form {
            height: 35vh;
            transform: translateY(-35vh);
            .cafe-wrap-search-result {
                max-height: 10vh;
            }
        }
    }
    .cafe-wrap-search:not(.normal) {
        --form-height: 60px;
        .cafe-wrap-search-fields {
            flex-direction: row;
        }
    }
}

/*End Search Form*/
/*Canvas Cart*/
.elementor-widget-clever-canvas-cart {
    --bg-color: #fff;
    --color: #000;
    --count-color: #fff;
    --count-bg-color: #f56f5e;
    .elementor-widget-container {
        font-size: 0;
    }
}

.cafe-wrap-icon-cart {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color);
    position: relative;
    transition: color 300ms;
    .cafe-cart-count {
        position: absolute;
        height: 20px;
        min-width: 20px;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        font-size: 0.8rem;
        display: flex;
        padding: 0 3px;
        justify-content: center;
        align-items: center;
    }
}

.cafe-cart-count {
    color: var(--count-color);
    background: var(--count-bg-color);
    line-height: 1;
}

.cafe-cart-subtotal {
    line-height: 1;
}

.cafe-wrap-right-cart {
    display: flex;
    align-items: center;
    color: var(--count-color);
}

.cafe-canvas-cart {
    display: inline-block;
    cursor: pointer;
    font-size: 1rem;
    .cafe-wrap-icon-cart {
        float: left;
    }
    &.subtotal-before .cafe-wrap-icon-cart {
        float: right;
    }
    &.count-top-right {
        .cafe-cart-count {
            top: 0;
            right: 0;
            transform: translate(50%, -50%);
        }
    }
    &.count-top-left {
        .cafe-cart-count {
            top: 0;
            left: 0;
        }
    }
    &.count-center {
        .cafe-cart-count {
            top: 50%;
            left: 50%;
        }
    }
    &.count-inline {
        --count-color: #000;
        --count-bg-color: transparent;
    }
    &:hover {
        .cafe-wrap-icon-cart {
            color: var(--color) !important;
        }
    }
}

.cafe-heading-cart-content {
    width: 100%;
    color: #000;
    font-size: 18px;
    letter-spacing: .5px;
    text-align: left;
    font-weight: 500;
    border-bottom: 1px solid var(--border-color);
    padding-bottom: 20px;
    display: inline-block;
    .cafe-cart-count {
        color: inherit;
        background: transparent;
        font-size: inherit !important;
    }
    .cafe-close-cart {
        float: right;
        font-size: 1rem;
        cursor: pointer;
        i {
            margin-left: 10px;
        }
    }
}

.cafe-canvas-cart-content {
    position: fixed;
    text-align: left;
    top: 0;
    right: 0;
    width: calc(100% - 50px);
    max-width: 480px;
    height: 100%;
    background: var(--bg-color);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    z-index: 99;
    visibility: hidden;
    transform: translateX(480px);
    transition: all 300ms;
    padding: 20px 15px;
    font-size: 1rem;
    .lazy-img:not(.loaded) {
        filter: blur(0);
    }
    .widget_shopping_cart_content {
        display: flex;
        flex-direction: column;
        height: calc(100% - 55px);
    }
    ul.woocommerce-mini-cart {
        max-height: 100%;
        overflow: hidden;
        overflow-y: auto;
        padding: 10px 0;
        flex-grow: 1;
    }
    .woocommerce-mini-cart__buttons {
        margin-bottom: 0;
    }
    .woocommerce-mini-cart__empty-message {
        padding: 30px 0;
        text-align: center;
    }
    &.loading {
        &:before {
            content: '';
            width: 100%;
            height: 100%;
            background: rgba(255, 255, 255, .6);
            position: absolute;
            left: 0;
            top: 0;
            display: block;
            z-index: 1;
        }
        &:after {
            width: 20px;
            height: 20px;
            background: #000;
            content: '';
            display: block;
            position: fixed;
            top: 50%;
            left: 50%;
            border-radius: 50%;
            transform: translate(-50% -50%);
            animation: blink 500ms infinite ease-in-out;
            z-index: 2;
        }
    }
}

.cafe-toggle-input:checked ~ {
    .cafe-canvas-cart-content {
        visibility: visible;
        transform: translateX(0);
    }
}

/*End Canvas Cart*/
/* Account widget*/
.elementor-widget-clever-account {
    --bg-modar: #fff;
    --color: #000;
}

.cafe-account-modal {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: var(--bg-modar);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    z-index: 110;
    transition: all 300ms;
    opacity: 0;
    visibility: hidden;
    .woocommerce-form input {
        margin-bottom: 0;
    }
    button {
        width: 100%;
    }
    .form-row {
        margin-bottom: 15px;
    }
    .wrap-register-toggle-button {
        display: none;
    }
    .cafe-notice{
        text-align: center;
        margin-bottom: 10px;
        font-size: 80%;
        color: var(--danger-color);
        transition: all 300ms;
        max-height: 100vh;
        overflow: hidden;
        width: 100%;
    }
    &:after{
        content: '';
        width: 20px;
        height: 20px;
        background: var(--black-color);
        position: absolute;
        left: 50%;
        top:50%;
        transform: translate(-50%, -50%);
        animation: blink 800ms infinite ease-in-out;
        z-index: 3;
        border-radius: 50%;
        visibility: hidden;
        transition: all 300ms;
    }
    &:before{
        width: 100%;
        height: 100%;
        content: '';
        top:0;
        left: 0;
        background: var(--white-color);
        opacity: 0;
        visibility: hidden;
        transition: all 300ms;
        position: absolute;
        display: block;
    }
    &.cafe-loading{
        .cafe-notice {
            max-height: 0;
        }
        &:after{
            visibility: visible;
        }
        &:before{
            visibility: visible;
            opacity: 0.4;
        }
    }

}
.heading-account-modal, .additional-login-info {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.heading-account-modal {
    margin-bottom: 30px;
    .lb-login {
        text-transform: uppercase;
        color: var(--color);
    }
}

.cafe-account-mask {
    background: none;
    position: fixed;
    top: 0;
    right: 0;
    left: auto;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color);
    font-size: 1.5rem;
    z-index: 111;
}

.cafe-toggle-input:checked ~ .cafe-account-modal {
    visibility: visible;
    opacity: 1;
}

.cafe-wrap-account {
    display: inline-block;
    position: relative;
    .woocommerce-MyAccount-navigation {
        position: absolute;
        top: calc(100% + 20px);
        opacity: 0;
        visibility: hidden;
        background: var(--bg-modar);
        padding: 20px 30px;
        z-index: 5;
        box-shadow: 0 3px 15px rgba(0, 0, 0, 0.1);
        min-width: 200px;
        transition: all 300ms;
        &.left {
            left: 0;
        }
        &.right {
            right: 0;
        }
        ul {
            margin: 0;
        }
        li {
            list-style: none;
            padding: 2px 0;
            margin: 0;
            white-space: nowrap;
            line-height: 1.3;
            font-size: 14px;
        }
        a {
            color: var(--color);
        }
    }
    &:hover {
        .woocommerce-MyAccount-navigation {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
        .cafe-account-btn {
            color: var(--color) !important;
        }
    }
}

.cafe-account-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color);
    cursor: pointer;
    i {
        display: flex;
        width: 30px;
        height: 30px;
        align-items: center;
        justify-content: center;
        transition: color 300ms;
    }
}

@media(min-width: 481.98px) {
    .cafe-canvas-cart-content {
        padding: 30px 40px 20px;
    }
    .cafe-account-modal {
        max-width: 430px;
        height: auto;
    }
    .cafe-account-mask {
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.6);
        color: #fff;
        display: block;
        z-index: 105;
        i {
            width: 50px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            float: right;
        }
    }
}

/* End Account widget*/
/*Wishlist & Compare*/
.cafe-wlcp {
    display: inline-block;
    --color: #000;
}

.cafe-wlcp-url {
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color);
}

.cafe-wlcp-icon {
    position: relative;
    i {
        display: flex;
        width: 30px;
        height: 30px;
        justify-content: center;
        align-items: center;
        transition: color 300ms
    }
}

.cafe-wlcp-label {
    transition: color 300ms
}

.cafe-wlcp-counter {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background: #f56f5e;
    min-width: 20px;
    height: 20px;
    color: #fff;
    border-radius: 50%;
    transition: all 300ms;
    &.top-left {
        right: 100%;
        bottom: 100%;
        transform: translate(50%, 50%);
    }
    &.top-right {
        left: 100%;
        bottom: 100%;
        transform: translate(-50%, 50%);
    }
    &.center {
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}

/*End Wishlist & Compare*/
/*Language switcher*/
.cafe-language-switcher {
    position: relative;
    .cafe-language-options {
        width: 100%;
        display: flex;
        align-items: center;
        &:after {
            font-family: cleversoft;
            content: '\c059';
            margin-right: 5px;
            margin-left: 5px;
            font-size: 75%;
        }
    }
    &:hover {
        .cafe-langs {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.cafe-langs {
    --box-shadow: #ebebeb;
    position: absolute;
    background: $white_text;
    left: 0;
    top: calc(100% + 15px);
    margin: 0;
    min-width: 150px;
    width: 100%;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition: all 300ms;
    box-shadow: 0 0 15px 10px var(--box-shadow);
    li {
        list-style: none;
        padding: 8px 15px;
        a {
            color: inherit;
        }
    }
}

/*End Language switcher*/