.cafe-headline.high-light{
    .cafe-words-wrapper{
        position: relative;
    }
    svg{
        position: absolute;
        width: calc(100% + 1rem);
        left: -0.5rem;
        z-index: -1;
        path{
            animation:cafe-path-dash 3s infinite;
            stroke:#f3bc34;
            stroke-width: 6;
            fill: none;
            stroke-dasharray: 1000;
            stroke-dashoffset: 1000;
        }
    }
    &.underline{
        svg{
            bottom:0;
        }
    }&.strike-through{
        svg{
            bottom:50%;
            transform: translateY(50%);
        }
    }&.brush-stroke{
        svg{
            bottom:50%;
            transform: translateY(50%);
        }
    }
    &.popout .cafe-words-wrapper{
        position: relative;
        display: inline-block;
        animation: cafe-text-popout 1s infinite alternate cubic-bezier(0.86, 0, 0.07, 1);
    }

}
@keyframes cafe-text-popout {
    0% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        text-shadow: 0em 0em 0 lightblue;
        color: black;
    }
    30% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        text-shadow: 0em 0em 0 lightblue;
        color: black;
    }
    70% {
        -webkit-transform: translate3d(0.08em, -0.08em, 0);
        transform: translate3d(0.08em, -0.08em, 0);
        text-shadow: -0.08em 0.08em lightblue;
        color: black;
    }
    100% {
        -webkit-transform: translate3d(0.08em, -0.08em, 0);
        transform: translate3d(0.08em, -0.08em, 0);
        text-shadow: -0.08em 0.08em lightblue;
        color: black;
    }
}
@keyframes cafe-path-dash{
    to {
        stroke-dashoffset: 0;
    }}
/*Random selected*/
.cafe-headline{
    --active-color:#E52E2E;
    --color:#666;
    margin:0;
   &.random-selected{
       span{
           line-height: inherit;
       }
       .cafe-words-wrapper{
           b{
               position: static;
               transition: all 300ms;
               color: var(--color);
               &:after{
                   content: ' / ';
                   color: var(--color);
                   font-size: 85%;
               }
                &.is-visible{
                    color: var(--active-color);
                    transition-delay: 300ms;
                }
           }
           b:last-child{
               &:after{
                   display: none;
               }
           }
       }
   }
}